.magnitudes-game {
  background-color: #333F50;
  color: #000;
  height: 630px;
  width: 1120px;
  position: relative;
  font-family: 'Calibri', sans-serif;
  display: flex;
  justify-content: center;
}

.menu-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: #C00000;
  color: #fff;
  border: none;
  padding: 8px 40px;
  border: 2px solid #ffffff;
  font-size: 14px;
  font-weight: bold;
  border-radius: 5px;
  z-index: 999;
}

.game-box {
  background-color: #FFFFFF;
  border-radius: 15px;
  padding: 20px;
  margin-top: 70px;
  width: 90%;
  height: 78%;
  position: relative;
  overflow: hidden;
}

.game-header-mag {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 18px;
  margin: 10px 20px;
}
.game-box h2{
  font-size: 28px;
  font-weight: 700;
  margin-left: 20px;
  margin-top: 10px;
}
.game-box li, .game-box p {
  font-size: 18px;
  line-height: 2;
}

.game-box p {
  margin-left: 20px;
  margin-right: 30px;
}
.grey-button {
  background-color: #e7e6e6;
  border: 1px solid #5f5f5f;
  border-radius: 5px;
  bottom: 5%;
  font-size: 14px;
  font-weight: 700;
  left: 50%;
  padding: 8px 60px;
  position: absolute;
  transform: translate(-50%, -50%);
  bottom : 23px !important;
}
.grey-buttons {
  background-color: #e7e6e6;
  border: 1px solid #5f5f5f;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  padding: 8px 30px;
}

.time-left {
  font-size: 22px;
}

.prompt-text {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  margin-top: 70px;
}

.magnitudes-game .options-container {
  display: flex;
  justify-content: center;
  gap: 150px;
  align-items: center;
  margin-top: 40px;
}

.magnitudes-game .option-box {
  width: 250px;
  height: 250px;
  border: 1.5px solid #000000;
  position: relative;
  cursor: pointer;
  background: #fff;
  
}

.fraction-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.fraction-top, .fraction-bottom {
  text-align: center;
  font-size: 60px;
}

.fraction-line {
  width: 110px;
  text-align: center;

  height: 4px;
  background: #000;
  margin: 20px 0;
}

.circle-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.circle-dot {
  position: absolute;
}

.summary-stats {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  margin-bottom: 30px;

}

.summary-item {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  padding-left: 25px;
}

.summary-title {
  font-size: 20px;
  font-weight: bold;
  width: 200px;
}

.summary-value {
  background-color: #E7E6E6;
  padding: 3px 15px;
  font-size: 14px;
  font-weight: bold;
  border-radius: 5px;
  width: 100px;
  text-align: center;
}

 .summary-table {
  margin-top: 20px;
  margin-left: 20px;
  display: flex;
  justify-content: left;
  max-height: 200px;
  overflow-y: auto;
  width: 96%;
  border: 0.5px solid #AAA9A8;
}

/* width */
::-webkit-scrollbar {
  width: 12px;
  border: 0px solid #000000;

}

/* Track */
::-webkit-scrollbar-track {
  background: #AAA9A8;
  border: 0px solid #000000;

}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #000000;
  border-radius: 3px;
  border: 0px solid #000000;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}


.summary-table table {
  border-collapse: collapse;
  height: 200px;
  width: 100%;
  
}

.summary-table th, .summary-table td {
  border: 1px solid #AAA9A8;
  text-align: center;
  height: 30px;
  font-size: 16px;
  width: 100px;
  
}
.summary-table th{
  background-color:#333F50;
  color: white;
  font-weight: bold;

}

.summary-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-top: 10px;
}

.click-to-view:hover {
  text-decoration: underline;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal-box {
  background: #fff;
  padding: 20px;
  display: flex;
  justify-content: center;
  gap: 150px;
  width: 700px;
  max-width: 90%;
  max-height: 70%;
}

.close-button {
  position: absolute;
  top: 128.5px;
  right: 190px;
  background: #000000;
  color: #fff;
  border: none;
  padding: 5px 8px;
  cursor: pointer;
}

.circle-preview {
  border: 1px solid #000;
  margin-top: 20px;
  width: 250px;
  height: 250px;
  display: flex;
  flex-flow: column;
}
.preview-box{
  display: flex;
  flex-flow: column;
  text-align: center;
  font-weight: bold;
  margin-bottom: 0px;
}
.preview-box p{
margin: 0px;
margin-top: 10px;
}
.summary-instructions{
  height: 150px;
}
